@mixin gc-meta-data() {
  color: $meta-data;
  font-size: 1.4rem;
  line-height: 2.4rem;
}

@mixin gc-meta-bolletje() {
  &:before {
    content: " ";
    speak: none;
    background: $border_color;
    width: .4rem;
    height: .4rem;
    margin: 1.1rem 1rem 0 .6rem;
    display: inline-block;
    float: left;
  }
}

.meta-data {
  color: $gc_greyblue;
  font-size: 1.4rem;
  line-height: 2.4rem;
  margin-top: 1.5rem;

  + p {
    margin-top: 2rem;
  }

  a {
    color: $meta-data;
  }

  dl,
  dd {
    margin: 0;
    padding: 0;
    display: inline-block;
  }

  dd {
    @include gc-meta-bolletje;
  }

  dt:first-child + dd:before {
    display: none;
  }
}

.meta-data > div,
.meta-data > span,
.meta-data__item {
  display: inline-block;

